:root {
  --primary-color: #fff;
  --secondary-color: #e255a3;
  --accent-color: #ff69b4;
  --background-color: #000;
  --overlay-bg: #000000d9;
  --controls-bg: #00000080;
  --shadow-color: #00000080;
  --border-radius: 1rem;
  --transition-duration: .3s;
  --z-index-overlay: 10000;
  --z-index-controls: 10001;
}

* {
  box-sizing: border-box;
}

html, body {
  background-color: var(--background-color);
  color: var(--primary-color);
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
}

canvas {
  cursor: grab;
  width: 100vw;
  height: 100vh;
  display: block;
}

canvas:active {
  cursor: grabbing;
}

#app {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.play-overlay {
  background: var(--overlay-bg);
  width: 100vw;
  height: 100vh;
  z-index: var(--z-index-overlay);
  transition: opacity var(--transition-duration) ease-in-out;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.play-overlay.hidden {
  opacity: 0;
  pointer-events: none;
}

.play-button {
  border-radius: var(--border-radius);
  background: var(--primary-color);
  color: var(--background-color);
  cursor: pointer;
  box-shadow: 0 4px 20px var(--shadow-color);
  transition: all var(--transition-duration) ease;
  border: none;
  flex-direction: column;
  align-items: center;
  gap: .5rem;
  padding: 1.5rem 3rem;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
}

.play-button:hover {
  box-shadow: 0 6px 25px var(--shadow-color);
  background: #f0f0f0;
  transform: translateY(-2px);
}

.play-button:active {
  box-shadow: 0 2px 10px var(--shadow-color);
  transform: translateY(0);
}

.play-button:focus {
  outline: 3px solid var(--secondary-color);
  outline-offset: 2px;
}

.play-icon {
  font-size: 2.5rem;
  line-height: 1;
}

.play-text {
  font-size: 1.2rem;
  font-weight: 500;
}

.audio-controls {
  z-index: var(--z-index-controls);
  background: var(--controls-bg);
  border-radius: var(--border-radius);
  box-shadow: 0 4px 20px var(--shadow-color);
  backdrop-filter: blur(10px);
  transition: opacity var(--transition-duration) ease;
  align-items: center;
  gap: 1rem;
  padding: .75rem 1.5rem;
  display: flex;
  position: fixed;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
}

.pause-button {
  background: var(--primary-color);
  width: 40px;
  height: 40px;
  color: var(--background-color);
  cursor: pointer;
  box-shadow: 0 2px 8px var(--shadow-color);
  transition: all var(--transition-duration) ease;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-weight: 600;
  display: flex;
}

.pause-button:hover {
  box-shadow: 0 4px 12px var(--shadow-color);
  background: #f0f0f0;
  transform: scale(1.1);
}

.pause-button:active {
  box-shadow: 0 1px 4px var(--shadow-color);
  transform: scale(.95);
}

.pause-button:focus {
  outline: 2px solid var(--secondary-color);
  outline-offset: 2px;
}

.pause-button.playing {
  background: var(--secondary-color);
  color: var(--primary-color);
}

.pause-button.playing .pause-icon {
  content: "â–¶";
}

.pause-icon {
  font-size: 1.2rem;
  line-height: 1;
}

.audio-controls.hidden {
  opacity: 0;
  pointer-events: none;
}

.audio-slider {
  cursor: pointer;
  width: 300px;
  height: 6px;
  transition: all var(--transition-duration) ease;
  background: #fff3;
  border-radius: 3px;
  outline: none;
}

.audio-slider::-webkit-slider-thumb {
  appearance: none;
  background: var(--primary-color);
  cursor: pointer;
  width: 18px;
  height: 18px;
  box-shadow: 0 2px 6px var(--shadow-color);
  transition: all var(--transition-duration) ease;
  border-radius: 50%;
}

.audio-slider::-webkit-slider-thumb:hover {
  box-shadow: 0 3px 8px var(--shadow-color);
  transform: scale(1.1);
}

.audio-slider::-moz-range-thumb {
  background: var(--primary-color);
  cursor: pointer;
  width: 18px;
  height: 18px;
  box-shadow: 0 2px 6px var(--shadow-color);
  transition: all var(--transition-duration) ease;
  border: none;
  border-radius: 50%;
}

.audio-slider::-moz-range-thumb:hover {
  box-shadow: 0 3px 8px var(--shadow-color);
  transform: scale(1.1);
}

.audio-slider:focus {
  outline: 2px solid var(--secondary-color);
  outline-offset: 2px;
}

.audio-time {
  color: var(--primary-color);
  text-align: center;
  min-width: 80px;
  font-family: Courier New, monospace;
  font-size: 1rem;
  font-weight: 500;
}

@media (width <= 768px) {
  .play-button {
    padding: 1rem 2rem;
    font-size: 1.5rem;
  }

  .play-icon {
    font-size: 2rem;
  }

  .play-text {
    font-size: 1rem;
  }

  .audio-controls {
    gap: .75rem;
    padding: .5rem 1rem;
    bottom: 16px;
  }

  .pause-button {
    width: 36px;
    height: 36px;
    font-size: 1rem;
  }

  .audio-slider {
    width: 200px;
  }

  .audio-time {
    min-width: 70px;
    font-size: .875rem;
  }
}

@media (width <= 480px) {
  .audio-slider {
    width: 150px;
  }

  .audio-controls {
    flex-direction: column;
    gap: .5rem;
    padding: .75rem;
  }

  .pause-button {
    width: 32px;
    height: 32px;
    font-size: .9rem;
  }
}

@media (prefers-contrast: high) {
  :root {
    --primary-color: #fff;
    --secondary-color: red;
    --accent-color: red;
    --overlay-bg: #000000f2;
    --controls-bg: #000c;
  }

  .play-button {
    border: 2px solid var(--primary-color);
  }

  .audio-slider::-webkit-slider-thumb {
    border: 2px solid var(--background-color);
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }

  .play-button:hover {
    transform: none;
  }

  .audio-slider::-webkit-slider-thumb:hover {
    transform: none;
  }
}

.play-button:focus-visible, .audio-slider:focus-visible {
  outline: 3px solid var(--secondary-color);
  outline-offset: 2px;
}

@media print {
  .play-overlay, .audio-controls {
    display: none !important;
  }
}
